import { Component, OnInit,DoCheck } from '@angular/core';
import { InformacionService } from '../../services/informacion.service';
import { Router } from '@angular/router';
import { SocketService } from '../../services/socket.service';

interface Datos {
  info;
  neg;
  blog;

}

@Component({
  selector: 'app-resultados',
  templateUrl: './resultados.component.html',
  styleUrls: ['./resultados.component.css']
})
export class ResultadosComponent implements OnInit,DoCheck {

  

  constructor(public _info:InformacionService,public router:Router,public _sk:SocketService) { }

  ngOnInit() {
    if(this._info.neg.length == 0 && this._info.blog.length == 0 && this._info.claro.length == 0 && this._info.places.length == 0 && !this._info.load) {
      this.router.navigate(['/inicio'])
    }

    
  }

  ngDoCheck() {
    if(this._info.texto != this._info.oldText) {
      this._info.oldText = this._info.texto;
      this._info.autocomplete(this._info.texto);
    }
  }

  deleteAuto() {
    this._info.dataAuto = [];
    
  }

  search(texto) {
    if(texto.length > 0) {
      this._info.texto = texto;
      this._info.oldText = texto;
      this._info.dataAuto = [];
      this._info.getSearch(texto);
    }
  }

  searchAuto(op) {
    this._info.texto = op;
    this._info.oldText = op;
    this._info.dataAuto = [];
    this.search(op);
  }

  checkArray(data) {
    if(Array.isArray(data)) return true;
    else return false;
  }

}



