import { Injectable } from '@angular/core';
import { ChatService } from './chat.service';

@Injectable()
export class MessagesService {

  estado = false;
  body = [];
  name = null;
  clave = null;

  constructor(private socket:ChatService) { }

  beginChat(clave,bn) {
    
    this.socket.emit('new-user',{clave,name:bn},(msg) => {
       this.body.push(msg);
       this.clave = clave;
    })
  }

  sendMsg(msg) {
    this.socket.emit('msg-user',{
      clave: this.clave,
      msg
    })
  }


  getMsg() {
    return this.socket.fromEvent('resp-emp');
  }

  getFecha() {
    let date = new Date();
    let day = this.zero(date.getDay());
    let mes = this.zero(date.getMonth());
    let ano = this.zero(date.getFullYear());
    let hora = this.zero(date.getHours());
    let min = this.zero(date.getMinutes());

    return hora+':'+min+' '+day+'/'+mes+'/'+ano;
  }

  zero(num) {
      let valor = '';
      if(num<10) valor = '0'+num;
      else valor = ''+num

      return valor;
  }

}



