import { Component, OnInit } from '@angular/core';
import { InformacionService } from '../../services/informacion.service';

@Component({
  selector: 'app-item-claro',
  templateUrl: './item-claro.component.html',
  styleUrls: ['./item-claro.component.css']
})
export class ItemClaroComponent implements OnInit {

  constructor(public _info:InformacionService) { }

  ngOnInit() {
    
  }

  porcentaje(original,oferta) {
    let r = ( (original - oferta) * 100) / original;
    r = Math.round(r);
    return r +'%';
  }

  title(tx) {
    let palabras = tx.split(' ');
    palabras = palabras.splice(0,4)
    return palabras.join(' ');
  }

  desp(tx) {
    let palabras = tx.split(' ');
    palabras = palabras.splice(0,10)
    return palabras.join(' ');
  }

  getMeta(url){   
    let img = new Image();
    img.onload = function(){
        alert( 'termono' );
    };
    return url;
  }

}



