import { Component, OnInit } from '@angular/core';
import { InformacionService } from '../../services/informacion.service';

@Component({
  selector: 'app-card-neg',
  templateUrl: './card-neg.component.html',
  styleUrls: ['./card-neg.component.css']
})
export class CardNegComponent implements OnInit {

  constructor(public _info:InformacionService) { }

  ngOnInit() {
  }

  getPagos(arreglo) {
    
    let nv = '';

    if(arreglo.length > 0) {
      for(let op of arreglo) {
        if(op.name == 'paymenttype') {
          for(let p of op.feature) {
            if(nv.length == 0) nv += p.content;
            else nv += ', '+p.content;
          }
  
          
        }
      }
    }

    return nv

  }

  getHrs(arreglo) {
    
    
    let arr = [];

    if(arreglo.length > 0) {
      for(let op of arreglo) {
        if(op.name == 'txtschedule') {
          //let nv = '';
          for(let p of op.feature) {
            //if(nv.length == 0) nv += p.value;
            //else nv += ' '+p.value;
            arr.push(p.value);
          }
          
          
        }
      }
    }

    return arr;

  }

}



