import { Routes,RouterModule } from '@angular/router';
import { InicioComponent } from './components/inicio/inicio.component';
import { ResultadosComponent } from './components/resultados/resultados.component';
import { ContenidoComponent } from './components/contenido/contenido.component';
import { ItemNegComponent } from './components/item-neg/item-neg.component';
import { ItemBlogComponent } from './components/item-blog/item-blog.component';
import { ItemClaroComponent } from './components/item-claro/item-claro.component';
import { ItemPlacesComponent } from './components/item-places/item-places.component';
import { ItemMapsComponent } from './components/item-maps/item-maps.component';

const rutas:Routes = [
    {
        path:'inicio',
        component: InicioComponent
    },
    {
        path: 'resultados',
        component: ResultadosComponent,
        children: [
            {
                path: 'negocios',
                component: ItemNegComponent
            },
            {
                path: 'blog',
                component: ItemBlogComponent
            },
            {
                path: 'productos',
                component: ItemClaroComponent
            },
            {
                path: 'google',
                component: ItemPlacesComponent
            },
            {
                path: 'mapa',
                component: ItemMapsComponent
            },
            {
                path: '**',
                pathMatch:'full',
                redirectTo: 'negocios'
            }
        ]
    },
    {
        path: '**',
        pathMatch:'full',
        redirectTo: 'inicio'
    }
]

export const RoutingModule = RouterModule.forRoot(rutas,{useHash: true})


