import { Component, OnInit, OnDestroy } from '@angular/core';
import { MessagesService } from '../../services/messages.service';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit,OnDestroy {

  mensaje:String = '';
  escuchando;

  constructor(public _msg:MessagesService) { }


  ngOnInit() {
    this.escuchando =  this._msg.getMsg().subscribe((resp) => {
      this._msg.body.push(resp);
      setTimeout(() => {
        let obj = document.getElementById('chat-history')
        obj.scrollTop = obj.scrollHeight;
      },100)
      
    })
  }

  salirChat() {
    this._msg.estado = false;
    this._msg.body = [];
  }

  addMsg(msg) {
    let texto = {
      name: 'USUARIO',
      fecha: this._msg.getFecha(),
      msg
    }
    
    this._msg.body.push(texto);
    this.mensaje = '';
    this._msg.sendMsg(msg);
    

    setTimeout(() => {
      let obj = document.getElementById('chat-history')
      obj.scrollTop = obj.scrollHeight;
    },100)
  }

  ngOnDestroy() {
    this.escuchando.unsubscribe();
  }



}



