import { Component, OnInit } from '@angular/core';
import { InformacionService } from '../../services/informacion.service';

@Component({
  selector: 'app-item-places',
  templateUrl: './item-places.component.html',
  styleUrls: ['./item-places.component.css']
})
export class ItemPlacesComponent implements OnInit {

  constructor(public _info:InformacionService) { }

  ngOnInit() {
  }

  getEstrellasActive(num) {
    let arr = [];
    for(let i=0;i<num;i++) {
      arr.push('i');
    }

    return arr;
  }

  getEstrellasNone(num) {
    let arr = [];
    num = 5 - num;
    for(let i=0;i<num;i++) {
      arr.push('i');
    }

    return arr;
  }

}



