import { Component, OnInit } from "@angular/core";
import { InformacionService } from "../../services/informacion.service";
import { MessagesService } from "../../services/messages.service";
declare const window: any;
@Component({
  selector: "app-item-neg",
  templateUrl: "./item-neg.component.html",
  styleUrls: ["./item-neg.component.css"]
})
export class ItemNegComponent {
  image = "";

  constructor(public _info: InformacionService, public _msg: MessagesService) {}

  openChat(clave, bn) {
    this._msg.estado = true;
    this._msg.name = bn;
    this._msg.beginChat(clave, bn);
  }

  getEstrellasActive(num) {
    let arr = [];
    for (let i = 0; i < num; i++) {
      arr.push("i");
    }

    return arr;
  }

  getEstrellasNone(num) {
    let arr = [];
    num = 5 - num;
    for (let i = 0; i < num; i++) {
      arr.push("i");
    }

    return arr;
  }

  getPys(pys) {
    let tx = "";
    let arr = [];
    let arr2 = [];
    let nuevo = [];

    //console.log('Arreglo: '+arreglo);
    if (pys.hasOwnProperty("prdserv") && pys.prdserv.length) {
      let arreglo = this._info.data.neg.pys;
      let data = pys.prdserv;

      if (Array.isArray(data)) {
        for (let op of data) {
          let nv = op.toUpperCase().trim();
          nuevo.push(nv);
        }

        for (let op of nuevo) {
          for (let op2 of arreglo) {
            if (op2 != op && !op.includes(op2)) arr2.push(op);
          }
        }

        data = [];

        for (let i = arreglo.length; i < 10 - arreglo.length; i++) {
          if (arr2[i] && arr2.length > 0) data.push(arr2[i]);
          else break;
        }

        let h = "";
        for (let op of arreglo) {
          if (h.length == 0) {
            h += `<strong>${op.toUpperCase()}</strong>`;
          } else {
            h += `, <strong>${op.toUpperCase()}</strong>`;
          }
        }

        if (data.length > 0) tx = h + ", " + data.join(", ");
        else tx = h;
      } else {
        let palabras = pys.prdserv.split(",");
        for (let op of palabras) {
          let nv = op.toLowerCase().trim();
          nuevo.push(nv);
        }

        for (let op of nuevo) {
          for (let op2 of arreglo) {
            if (op2 != op && !op.includes(op2)) arr2.push(op);
          }
        }

        tx = arr2.join(", ");
      }
    }

    return tx;
  }

  getPagos(arreglo) {
    let nv = "";

    if (arreglo.length > 0) {
      for (let op of arreglo) {
        if (op.name == "paymenttype") {
          for (let p of op.feature) {
            if (nv.length == 0) nv += p.content;
            else nv += ", " + p.content;
          }
        }
      }
    }

    return nv;
  }

  getHrs(arreglo) {
    let arr = [];

    if (arreglo.length > 0) {
      for (let op of arreglo) {
        if (op.name == "txtschedule") {
          //let nv = '';
          for (let p of op.feature) {
            arr.push(p.value);
          }
        }
      }
    }

    return arr;
  }

  getDist(num) {
    let valor;
    if (num < 1) {
      num = num * 1000;
      num = num.toFixed(1);
      valor = num + " m";
    } else {
      num = num.toFixed(1);
      valor = num + " Km";
    }

    return valor;
  }

  call(event, id: number): void {
    navigator.getUserMedia(
      { audio: true },
      localMediaStream => {
        window.Overtok.call('call', id);
      },
      error => {
        alert('Permite el acceso al micrfono para  poder realizar llamadas');
      }
    );
  }
}



