import { Component, OnInit,DoCheck } from '@angular/core';
import { InformacionService } from '../../services/informacion.service';



@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.css']
})
export class InicioComponent implements OnInit,DoCheck {

  constructor(public _info:InformacionService) { }

  ngOnInit() {
    this._info.getHome();
    
  }

  ngDoCheck() {
    if(this._info.texto != this._info.oldText) {
      this._info.oldText = this._info.texto;
      this._info.autocomplete(this._info.texto);
    }
  }

  search(texto) {
    if(texto.length > 0) {
      this._info.texto = texto;
      this._info.oldText = texto;
      this._info.dataAuto = [];
      this._info.getSearch(texto);
    }
  }

  searchAuto(op) {
    this._info.texto = op;
    this._info.oldText = op;
    this._info.dataAuto = [];
    this.search(op);
  }

  deleteAuto() {
    this._info.dataAuto = [];
  }


}



