import { Http } from '@angular/http';
import { Injectable } from '@angular/core';
import { SocketService } from './socket.service';
import { Router } from '@angular/router';

interface Datos {
  info;
  neg;
  blog;
  total;

}

declare var autocomplete:any;


@Injectable()
export class InformacionService {

  constructor(public _sk:SocketService,public router:Router,public http:Http) {

  }

  texto:String = '';
  coors = {
    lat:null,
    lng:null
  }
  enter = true;
  neg = [];
  blog = [];
  claro = [];
  places = [];
  comments = [];
  data = {
    neg:null,
    where:null,
    ruta:null
  };
  cardBlog = {};
  cardProd = {};
  negHrs = [];
  negCoors = [];
  component_lat = 23.0000000;
  component_lng = -102.0000000;
  zoom = 8;
  load = false;
  i = 0;
  empty = false;
  dataAuto = [];
  clima;
  oldText:String = '';
  totalNeg = 0;
  totalBlog = 0;
  totalClaro = 0;
  pageNeg = 0;
  pageBlog = 0;
  pageClaro = 0;
  //Unsuscribe

  

  autocomplete(texto) {
    
    if(texto.length >= 3) {
      texto = texto.trim();
      this._sk.sendAuto(texto);
      let auto = this._sk.getAuto().subscribe((resp:Datos) => {
        this.dataAuto = resp.info;
        //
        //console.log(this.dataAuto);
        //autocomplete(document.getElementById("tags"), this.dataAuto);
        auto.unsubscribe();
      });
    }
    else {
      this.dataAuto = [];
    }
    

  }

  getHome() {
    
    this.clearData();
    this._sk.sendHome();
    let home = this._sk.getHome().subscribe((resp:Datos) => {
      //console.log(resp);
      this.neg = resp.neg;
      this.blog = resp.blog;
      home.unsubscribe();
    })
  }

  getOther(modo,tipo,page) {
    window.scrollTo(0, 0)
    this.load = true;
    if(modo == 'asc') page++;
    else if(modo == 'desc') page--;
    //console.log('========>'+page+'<=========')
    this._sk.sendOther(tipo,page,this.data);
    if(tipo == 'neg') {
      let dataNeg = this._sk.getNeg().subscribe((resp:Datos) => {
        this.load = false;
        //this.i++;
        //if(this.i>=4) this.backHome();
        ///if(this.load && resp.info.length > 0) this.load = false; 
        //if(!this.load && resp.info.length > 0 && url && this.data.ruta == 'negocios') this.router.navigate([url])
        this.neg = resp.info;
        //this.totalNeg = resp.total;
        this.negHrs = this.neg.slice(0,3);
        this.negCoors = [];
        for(let op of this.neg) {
          if(op.pin.length > 0) {
            this.negCoors.push({
              title: op.bn,
              lat: op.pin[1],
              lng: op.pin[0],
              img: 'assets/images/seccion-ico.jpg',
              phone: (op.phones.hasOwnProperty('phone') && op.phones.phone.length > 0) ? op.phones.phone[0].number:null,
              street: op.fullstreet+','+ op.colony +', '+ op.physicalcity +', '+ (op.statename.length > 0 ? op.statename: op.state) +', '+ op.zip
            })
          }
        }
  
        if(this.negCoors.length > 0) {
          this.component_lat = (Object.keys(this.data.where.maps).length > 0 && this.data.where.maps.lat) ? this.data.where.maps.lat:this.negCoors[0].lat;
          this.component_lng = (Object.keys(this.data.where.maps).length > 0 && this.data.where.maps.lng) ? this.data.where.maps.lng:this.negCoors[0].lng;
          if(Object.keys(this.data.where.maps).length > 0 && this.data.where.maps.lng) {
            this.negCoors.push({
              title: null,
              lat: this.data.where.maps.lat,
              lng: this.data.where.maps.lng,
              img: 'http://www.ribola.hr/Themes/Ribola/Content/images/marker-red.png',
              phone: null,
              street: null
            })
          }
          
        }
        dataNeg.unsubscribe();
  
      })
      let dataComments = this._sk.getComments().subscribe((resp:Datos) => {
        let t2 = new Date().getTime();
        ////('Comments: '+((t2-t1)/1000))
        this.comments = resp.info;
        dataComments.unsubscribe();
      })
      this.pageNeg = page;
    }
    else if(tipo == 'claro') {
      let dataClaro = this._sk.getClaroShop().subscribe((resp:Datos) => {
        this.load = false;
        this.claro = resp.info;
        this.totalClaro = resp.total;
        this.cardProd = this.claro[0];
        //console.log(this.totalClaro);
        dataClaro.unsubscribe();
  
      })
      this.pageClaro = page
    }
    else if(tipo == 'blog') {
      let dataBlog = this._sk.getBlog().subscribe((resp:Datos) => {
        this.load = false;
        this.blog = resp.info;
        this.totalBlog = resp.total;
        //this.cardBlog = this.blog[0];
        //console.log(this.totalBlog)
        dataBlog.unsubscribe();
        
      })
      this.pageBlog = page;
    }
  }

  getSearch(texto) {
    window.scrollTo(0, 0)
    this.i = 0;
    this.load = true;
    let url = null;
    this.router.navigate(['/resultados/blog']);
    this.clearData();
    let t1 = new Date().getTime();
    if(this.enter) {
      this._sk.sendSearch(texto,this.coors.lat,this.coors.lng);
      this.enter = false;
    }
    //Solicitudes
    let dataClima = this._sk.getClima().subscribe((resp:Datos) => {
      //console.log('*********Clima*************');
      //console.log(resp);
      this.clima = resp.info;
      dataClima.unsubscribe();
    })
    
    let dataAll = this._sk.getData().subscribe((resp:Datos) => {
      this.i++;
      //if(this.i >= 6) this._sk.bye();
      this.data = resp.info;
      //console.log(this.data);
      this.zoom = 13
      if(this.data.ruta) {
        url = '/resultados/'+this.data.ruta
      }
      dataAll.unsubscribe();

    })
    let dataNeg = this._sk.getNeg().subscribe((resp:Datos) => {
      this.i++;
      if(this.i>=5) this.backHome();
      //if(this.i >= 6) this._sk.bye();
      if(this.load && resp.info.length > 0) this.load = false; 
      if(!this.load && resp.info.length > 0 && url && this.data.ruta == 'negocios') this.router.navigate([url])
      let t2 = new Date().getTime();
      //console.log('Negocios: '+((t2-t1)/1000))
      this.neg = resp.info;
      this.totalNeg = resp.total;
      this.negHrs = this.neg.slice(0,3);
      this.negCoors = [];
      for(let op of this.neg) {
        if(op.pin.length > 0) {
          this.negCoors.push({
            title: op.bn,
            lat: op.pin[1],
            lng: op.pin[0],
            img: 'assets/images/ico_seccion.png',
            phone: (op.phones.hasOwnProperty('phone') && op.phones.phone.length > 0) ? op.phones.phone[0].number:null,
            street: op.fullstreet+','+ op.colony +', '+ op.physicalcity +', '+ (op.statename.length > 0 ? op.statename: op.state) +', '+ op.zip
          })
        }
      }

      if(this.negCoors.length > 0) {
        this.component_lat = (Object.keys(this.data.where.maps).length > 0 && this.data.where.maps.lat) ? this.data.where.maps.lat:this.negCoors[0].lat;
        this.component_lng = (Object.keys(this.data.where.maps).length > 0 && this.data.where.maps.lng) ? this.data.where.maps.lng:this.negCoors[0].lng;
        if(Object.keys(this.data.where.maps).length > 0 && this.data.where.maps.lng) {
          this.negCoors.push({
            title: null,
            lat: this.data.where.maps.lat,
            lng: this.data.where.maps.lng,
            img: 'assets/images/ico_mapa.png',
            phone: null,
            street: null
          })
        }
        
      }
      //console.log(this.neg);
      dataNeg.unsubscribe();

    })
    let dataClaro = this._sk.getClaroShop().subscribe((resp:Datos) => {
      this.i++;
      if(this.i>=5) this.backHome();
      //if(this.i >= 6) this._sk.bye();
      if(this.load && resp.info.length > 0) this.load = false; 
      if(!this.load  && resp.info.length > 0 && url && this.data.ruta == 'productos') this.router.navigate([url])
      let t2 = new Date().getTime();
      //console.log('Claro: '+((t2-t1)/1000))
      this.claro = resp.info;
      this.totalClaro = resp.total;
      this.cardProd = this.claro[0];
      //console.log(this.totalClaro);
      dataClaro.unsubscribe();

    })
    let dataBlog = this._sk.getBlog().subscribe((resp:Datos) => {
      this.i++;
      if(this.i>=5) this.backHome();
      //if(this.i >= 6) this._sk.bye();
      if(this.load && resp.info.length > 0) this.load = false; 
      if(!this.load && resp.info.length > 0 && url && this.data.ruta == 'blog') this.router.navigate([url])
      let t2 = new Date().getTime();
      //console.log('Blog: '+((t2-t1)/1000))
      this.blog = resp.info;
      this.totalBlog = resp.total;
      this.cardBlog = this.blog[0];
      //console.log(this.totalBlog)
      dataBlog.unsubscribe();
      
    })
    let dataPlaces = this._sk.getPlaces().subscribe((resp:Datos) => {
      this.i++;
      if(this.i>=5) this.backHome();
      if(this.load && resp.info.length > 0) this.load = false; 
      if(!this.load && resp.info.length > 0 && !this.data.ruta && resp.info.length > 0) this.router.navigate(['/resultados/google'])
      let t2 = new Date().getTime();
      //console.log('Places: '+((t2-t1)/1000))
      this.places = resp.info;
      for(let op of this.places) {
        this.negCoors.push({
          title: op.name,
          lat: op.geometry.location.lat,
          lng: op.geometry.location.lng,
          img: 'assets/images/ico_google.png',
          phone:null,
          street: op.formatted_address          
        })
      }
      //console.log(this.places);
      dataPlaces.unsubscribe();
      
    })
    let dataComments = this._sk.getComments().subscribe((resp:Datos) => {
      this.i++;
      //if(this.i >= 6) this._sk.bye();
      let t2 = new Date().getTime();
      //console.log('Comments: '+((t2-t1)/1000))
      this.comments = resp.info;
      dataComments.unsubscribe();
    })
    
    /*this._sk.getSearch().subscribe((resp:Datos) => {
      //console.log('Respuesta Search =====>');
      //console.log(resp);
      this.neg = resp.neg
      this.blog = resp.blog;
      this.claro = resp.claro_shop;
      this.places = resp.places;
      console.log(this.places);
      let ruta = '/resultados/'+resp.ind;
      this.router.navigate([ruta]);
    })*/


    setTimeout(() => {
      this.enter = true;

    },100)

  }

  backHome() {

    if(this.neg.length == 0 && this.blog.length == 0 && this.claro.length == 0 && this.places.length == 0) {
      this.router.navigate(['/inicio'])
    }
  }

  clearData() {
    this.claro = [];
    this.blog = [];
    this.neg = [];
    this.comments = [];
    this.places = [];
    this.cardBlog = {};
    this.cardProd = {};
    this.negCoors = [];
    this.negHrs = [];
    this.clima = null;
    this.pageBlog = 0;
    this.pageClaro = 0;
    this.pageNeg = 0;
    this.totalBlog = 0;
    this.totalClaro = 0;
    this.totalNeg = 0;
  }

  setZoom(where) {
    if((where.estado && where.city && where.colony) || ( Object.keys(where.maps).length > 0 && where.maps.dir.estado && where.maps.dir.city && where.maps.dir.colony)) return 14;
    else if((where.estado && where.city) || ( Object.keys(where.maps).length > 0 && where.maps.dir.estado && where.maps.dir.city)) return 11;
    else if((where.estado) || ( Object.keys(where.maps).length > 0 && where.maps.dir.estado)) return 9;
    else return 8;
  }

}






