import { Component,OnInit } from '@angular/core';
import { InformacionService } from './services/informacion.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'app';

  constructor(public _info:InformacionService) {}

  ngOnInit() {
    if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((pos) => {
          this._info.coors.lat = pos.coords.latitude;
          this._info.coors.lng = pos.coords.longitude;
          //console.log('Lat: '+this._info.coors.lat);
          //console.log('Lng: '+this._info.coors.lng);
        });
    }
    else {
      this._info.coors.lat = null;
      this._info.coors.lng = null;
      //console.log('Lat: '+this._info.coors.lat);
      //console.log('Lng: '+this._info.coors.lng);
    }
  }


}



